/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql, Link } from "gatsby"
import Header from "./header"
import { Box, Footer, Grommet, Button } from 'grommet'
import { deepFreeze } from "grommet/utils"
import { BiCopyright } from "react-icons/bi"
import zap from '../images/power-zap-gray.svg';

const Layout = ({ background, children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <Box align='center'>
      <Box
        tag='header'
        direction='row'
        align='center'
        justify='between'
        background={background}
        height='fit-content'
        elevation='medium'
        round='small'
        width='large'
      >
        <Box direction='row' pad='small'>
          {data.site.siteMetadata?.title.toLowerCase() || `Title`}
        </Box>
        <Box direction='row' gap='medium' pad='small'>
          <Link to='/'>
            <Button plain style={{ color: '#444444', fontSize: '14px' }} label="dashboard" />
          </Link>
          <Link to='/about'>
            <Button plain style={{ color: '#444444', fontSize: '14px' }} label="about" />
          </Link>
        </Box>
      </Box>
      <Box> 
        <Box margin="small" pad="small" height='fit-content' align="center">{children}</Box>
        <Footer align='start' direction="column" style={{ fontSize: "10px" }} gap='xxsmall' justify='start'>
          <Box direction='row' gap='xxsmall'>
            <BiCopyright />{new Date().getFullYear()}, built with <a href="https://gatsbyjs.com">gatsby</a>, <a href="https://www.coingecko.com/en/api" target="_blank">coingecko</a>, and zapper
          </Box>
          <Box margin={{ left: '-15px' }}>
            <a href="https://zapper.fi" target="_blank"><img width='200px' style={{ opacity: 0.4 }} src={zap} /></a>
          </Box>
        </Footer>
      </Box>
    </Box>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
